import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".splide__track{height:100%}.splide_slide{width:100%}.splide__pagination{justify-content:flex-start}.splide__pagination__page{height:11px;width:11px}.splide__pagination__page.is-active{background:#fff;border-radius:12.375px;height:9px;margin-left:8px;margin-right:8px;width:24px}@media screen and (max-width:968px){.login-form-cont{width:90%!important}.image-view-cont>p{font-size:19.6966px!important;line-height:33px!important}.image-view-cont{background-size:90%!important}.social-login-cont>a{padding:7px 10px!important}}@media screen and (max-width:768px){.login-left-cont,.login-pics-cont{width:100%!important}.login-pics-cont{margin-bottom:10px;min-height:403px;padding:21px!important}.login-main-cont{flex-wrap:wrap-reverse;padding:12px!important}.social-login-cont>a{margin-bottom:5px;width:100%}.social-login-cont{flex-wrap:wrap}.image-view-cont{background-size:45%!important}.image-view-cont>img{order:2}.image-view-cont>p{font-size:15px!important;line-height:23px!important;order:1}}@media screen and (max-width:576px){.login-pics-cont{min-height:253px!important}}", "",{"version":3,"sources":["webpack://./resources/js/components/auth/Login.vue"],"names":[],"mappings":"AAg8BA,eACA,WACA,CACA,cACA,UACA,CACA,oBACA,0BACA,CACA,0BAEA,WAAA,CADA,UAEA,CACA,oCACA,eAAA,CACA,sBAAA,CAEA,UAAA,CACA,eAAA,CACA,gBAAA,CAHA,UAIA,CACA,oCACA,iBACA,mBACA,CACA,mBACA,6BAAA,CACA,0BACA,CACA,iBACA,6BACA,CACA,qBACA,0BACA,CACA,CACA,oCAIA,kCAFA,oBAOA,CALA,iBAIA,kBAAA,CADA,gBAAA,CADA,sBAGA,CACA,iBACA,sBAAA,CACA,sBACA,CACA,qBAEA,iBAAA,CADA,UAEA,CACA,mBACA,cACA,CACA,iBACA,6BACA,CACA,qBACA,OACA,CACA,mBACA,wBAAA,CACA,0BAAA,CACA,OACA,CACA,CACA,oCACA,iBACA,0BACA,CACA","sourcesContent":["<template>\r\n    <div class=\"login-main-cont\">\r\n        <div class=\"login-left-cont\">\r\n            <div class=\"login-form-cont\">\r\n                <h1>Welcome to Happywed !</h1>\r\n                <p v-if=\"url === 'host'\">\r\n                    Wedding Organizers! you have access to a range of tools that\r\n                    make managing your wedding events easier. Login now to\r\n                    create and manage your wedding invitations, track RSVPs, and\r\n                    communicate with your guests.\r\n                </p>\r\n                <p v-else-if=\"url === 'vendor'\">\r\n                    Vendors! As a member of our community, you have access to\r\n                    our platform to showcase your professional services to\r\n                    couples seeking your expertise. Simply log in to your\r\n                    account to manage your business and connect with potential\r\n                    clients.\r\n                </p>\r\n                <p v-else-if=\"url === 'guest'\">\r\n                    Guests! you can log in to your account to view your wedding\r\n                    invitation, RSVP, and provide your family member's details,\r\n                    as well as your arrival and departure details. Log in now to\r\n                    ensure your attendance is counted and you don't miss out on\r\n                    any important wedding details.\r\n                </p>\r\n                <p v-else>Welcome! Wedding Community Members</p>\r\n\r\n                <!-- login with password -->\r\n                <form\r\n                    @submit.prevent\r\n                    v-if=\"loginWithOtp == false\"\r\n                    method=\"POST\"\r\n                >\r\n                    <input type=\"hidden\" name=\"_token\" v-bind:value=\"csrf\" />\r\n                    <label for=\"email\" class=\"login-form-group\">\r\n                        E-Mail Address/ Mobile\r\n                        <input\r\n                            type=\"text\"\r\n                            name=\"email\"\r\n                            id=\"email\"\r\n                            v-model=\"email\"\r\n                            @change=\"emptyData()\"\r\n                            @input=\"emptyData()\"\r\n                        />\r\n                        <span\r\n                            v-if=\"errorSubmit && errorSubmit.email\"\r\n                            class=\"errMsg\"\r\n                            >{{ errorSubmit.email[0] }}</span\r\n                        >\r\n                    </label>\r\n\r\n                    <div\r\n                        v-if=\"multiUsersExist == true\"\r\n                        class=\"multiple-users\"\r\n                        style=\"display: flex; justify-content: space-between\"\r\n                    >\r\n                        <span\r\n                            @click=\"\r\n                                selectedUser = item;\r\n                                active = item;\r\n                            \"\r\n                            :class=\"selectedUser == item ? 'active' : ''\"\r\n                            v-for=\"(item, index) in multiUsers\"\r\n                            :key=\"index\"\r\n                        >\r\n                            {{ item }}\r\n                        </span>\r\n                    </div>\r\n                    <div\r\n                        v-if=\"\r\n                            (userExists && multiUsersExist == false) ||\r\n                            selectedUser != null\r\n                        \"\r\n                    >\r\n                        <label for=\"password\" class=\"login-form-group\">\r\n                            Password\r\n                            <input\r\n                                :type=\"showPass\"\r\n                                name=\"password\"\r\n                                id=\"pass\"\r\n                                v-model=\"password\"\r\n                            />\r\n                            <img\r\n                                @click=\"showPass = 'text'\"\r\n                                v-if=\"showPass == 'password'\"\r\n                                src=\"/assets/login/eye.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <img\r\n                                @click=\"showPass = 'password'\"\r\n                                v-if=\"showPass == 'text'\"\r\n                                src=\"/assets/login/eye-off.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <span\r\n                                v-if=\"errorSubmit && errorSubmit.password\"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.password[0] }}</span\r\n                            >\r\n                        </label>\r\n\r\n                        <div class=\"forn-grp-dbl\">\r\n                            <label\r\n                                for=\"remember_token\"\r\n                                class=\"login-form-group\"\r\n                                style=\"flex-direction: row\"\r\n                            >\r\n                                <input\r\n                                    type=\"checkbox\"\r\n                                    name=\"remember_token\"\r\n                                    id=\"remember_token\"\r\n                                    value=\"true\"\r\n                                    v-model=\"remember_token\"\r\n                                />\r\n                                <span>Keep me logged in</span>\r\n                            </label>\r\n                            <a\r\n                                class=\"forgot-link\"\r\n                                :href=\"route(url + 'password.request')\"\r\n                                >Forgot Password?</a\r\n                            >\r\n                        </div>\r\n                    </div>\r\n                    <span v-if=\"errMsg\" class=\"errMsg\">{{ errMsg }}</span>\r\n                    <button\r\n                        v-if=\"selectedUser == null\"\r\n                        @click=\"checkUserExists()\"\r\n                        type=\"button\"\r\n                        :disabled=\"disableLogIn\"\r\n                    >\r\n                        Continue\r\n                    </button>\r\n                    <button\r\n                        v-if=\"selectedUser != null\"\r\n                        @click=\"loginUser\"\r\n                        type=\"button\"\r\n                        :disabled=\"disableLogIn\"\r\n                    >\r\n                        Log In\r\n                    </button>\r\n                </form>\r\n                <!-- login with otp -->\r\n                <form v-if=\"loginWithOtp == true\" @submit.prevent=\"verifyOtp\">\r\n                    <!-- <input type=\"hidden\" name=\"_token\" v-bind:value=\"csrf\" /> -->\r\n                    <label for=\"email\" class=\"login-form-group\">\r\n                        Mobile\r\n                        <input\r\n                            type=\"text\"\r\n                            name=\"mobile\"\r\n                            id=\"mobile\"\r\n                            v-model=\"email\"\r\n                        />\r\n                        <span\r\n                            v-if=\"asyncErrorSubmit && asyncErrorSubmit.mobile\"\r\n                            class=\"errMsg\"\r\n                            >{{ asyncErrorSubmit.mobile[0] }}</span\r\n                        >\r\n                    </label>\r\n\r\n                    <div\r\n                        v-if=\"multiUsersExist == true\"\r\n                        class=\"multiple-users\"\r\n                        style=\"display: flex; justify-content: space-between\"\r\n                    >\r\n                        <span\r\n                            @click=\"\r\n                                selectedUser = item;\r\n                                active = item;\r\n                            \"\r\n                            v-for=\"(item, index) in multiUsers\"\r\n                            :class=\"selectedUser == item ? 'active' : ''\"\r\n                            :key=\"index\"\r\n                        >\r\n                            {{ item }}\r\n                        </span>\r\n                    </div>\r\n\r\n                    <label\r\n                        v-if=\"otpSent == true\"\r\n                        for=\"otp\"\r\n                        class=\"login-form-group\"\r\n                    >\r\n                        OTP\r\n                        <input type=\"text\" name=\"otp\" id=\"otp\" v-model=\"otp\" />\r\n                        <span\r\n                            v-if=\"errorSubmit && errorSubmit.otp\"\r\n                            class=\"errMsg\"\r\n                            >{{ errorSubmit.otp[0] }}</span\r\n                        >\r\n                    </label>\r\n                    <button\r\n                        @click=\"sendOtp\"\r\n                        v-if=\"otpSent == false\"\r\n                        type=\"button\"\r\n                        :disabled=\"disableLogIn\"\r\n                    >\r\n                        Send Otp\r\n                    </button>\r\n                    <div v-else>\r\n                        <button type=\"submit\" :disabled=\"disableLogIn\">\r\n                            Log In\r\n                        </button>\r\n                        <button\r\n                            :disabled=\"disableLogIn\"\r\n                            @click=\"retryOtp\"\r\n                            type=\"button\"\r\n                        >\r\n                            Resend OTP\r\n                        </button>\r\n                    </div>\r\n                </form>\r\n\r\n                <!-- \"social-login-cont\" was earlier class applied and need to removed  -->\r\n                <div\r\n                    class=\"d-flex justify-content-center\"\r\n                    v-if=\"url == 'host' || url == 'vendor' || url == 'admin'\"\r\n                >\r\n                    <!-- <i class=\"text-small\">login with </i> -->\r\n                    <a class=\"btn btn-sm mx-3\" :href=\"route('googleLogin')\">\r\n                        <img\r\n                            src=\"/assets/login/google.svg\"\r\n                            alt=\"\"\r\n                            style=\"margin-right: 4px\"\r\n                        />\r\n                        <!-- Log in with Google -->\r\n                    </a>\r\n                    <a class=\"btn btn-sm mx-3\" :href=\"route('fblogin')\">\r\n                        <img\r\n                            src=\"/assets/login/logos_facebook.svg\"\r\n                            alt=\"\"\r\n                            style=\"margin-right: 4px\"\r\n                        />\r\n                        <!-- Log in with Facebook-->\r\n                    </a>\r\n                </div>\r\n                <!-- login with OTP button -->\r\n                <div\r\n                    v-if=\"\r\n                        (userExists &&\r\n                            multiUsersExist == false &&\r\n                            loginWithOtp != true &&\r\n                            isNumericString(email)) ||\r\n                        (selectedUser != null &&\r\n                            isNumericString(email) &&\r\n                            loginWithOtp != true)\r\n                    \"\r\n                    class=\"social-login-cont\"\r\n                >\r\n                    <div @click=\"loginWithOtp = true\">\r\n                        <i class=\"fas fa-sms\"></i>\r\n                        Log in with OTP\r\n                    </div>\r\n                </div>\r\n                <!-- login with password button -->\r\n                <div\r\n                    v-if=\"\r\n                        (userExists &&\r\n                            multiUsersExist == false &&\r\n                            loginWithOtp == true) ||\r\n                        (selectedUser != null &&\r\n                            !isNumericString(email) &&\r\n                            loginWithOtp == true) ||\r\n                        loginWithOtp == true\r\n                    \"\r\n                    class=\"social-login-cont\"\r\n                >\r\n                    <div @click=\"loginWithOtp = false\">\r\n                        <i class=\"fas fa-sms\"></i>\r\n                        Log in with Password\r\n                    </div>\r\n                </div>\r\n                <div v-if=\"url == 'host'\" class=\"sinup-link-cont\">\r\n                    Not a member? <a :href=\"route('hostRegister')\">Sign up</a>\r\n                </div>\r\n                <div v-else-if=\"url == 'vendor'\" class=\"sinup-link-cont\">\r\n                    Not a member? <a :href=\"route('vendorRegister')\">Sign up</a>\r\n                </div>\r\n                <div v-else-if=\"url == 'admin'\" class=\"sinup-link-cont\">\r\n                    Not a member? <a :href=\"route('adminRegister')\">Sign up</a>\r\n                </div>\r\n                <div v-else class=\"sinup-link-cont\"></div>\r\n                <!-- <div class=\"login-toggle-cont\">\r\n                    <a :href=\"route('hostLogin')\" v-if=\"active != 'host'\">\r\n                        Host\r\n                    </a>\r\n                    <a :href=\"route('login')\" v-if=\"active != 'guest'\">\r\n                        Guest\r\n                    </a>\r\n                    <a :href=\"route('brideLogin')\" v-if=\"active != 'bride'\">\r\n                        Bride\r\n                    </a>\r\n                    <a :href=\"route('groomLogin')\" v-if=\"active != 'groom'\">\r\n                        Groom\r\n                    </a>\r\n                    <a :href=\"route('vendorLogin')\" v-if=\"active != 'vendor'\">\r\n                        Vendor\r\n                    </a>\r\n                    <a :href=\"route('companyLogin')\" v-if=\"active != 'company'\">\r\n                        Planner\r\n                    </a>\r\n                    <a :href=\"route('memberLogin')\" v-if=\"active != 'member'\">\r\n                        Member\r\n                    </a>\r\n                </div> -->\r\n            </div>\r\n        </div>\r\n        <div class=\"login-pics-cont\">\r\n            <div class=\"splide\" id=\"login-splide-cont\">\r\n                <div class=\"splide__track\">\r\n                    <ul class=\"splide__list\">\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img1.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    The easiest way to get from 'Save the Date'\r\n                                    to 'thanks so much'. Designed and customized\r\n                                    by you.\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img7.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    The online RSVP that's so simple and excited\r\n                                    for you & your guests.\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img6.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    Decide who to celebrate with when and where,\r\n                                    we'll help the rest-from collecting contact\r\n                                    info to managing RSVPs.\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img5.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    Find pre-screened vendors and get\r\n                                    personalised recs. Reach out here and book\r\n                                    when you're ready.\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                    </ul>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <FlashMessage :message=\"message\"></FlashMessage>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport FlashMessage from \"@/components/FlashMessage.vue\";\r\n\r\nexport default {\r\n    props: [\"errorSubmit\", \"oldvalues\", \"url\"],\r\n    components: {\r\n        FlashMessage,\r\n    },\r\n    data() {\r\n        return {\r\n            splider: null,\r\n            active: null,\r\n            disableLogIn: false,\r\n            email: this.oldvalues[\"email\"] ?? null,\r\n            mobile: this.oldvalues[\"mobile\"] ?? null,\r\n            password: this.oldvalues[\"password\"] ?? null,\r\n            otp: this.oldvalues[\"otp\"] ?? null,\r\n            remember_token: this.oldvalues[\"remember_token\"] ?? false,\r\n            //formRoute: route(\"hostLogin\"),\r\n            csrf: $('meta[name=\"csrf-token\"]').attr(\"content\"),\r\n            showPass: \"password\",\r\n            loginWithOtp: false,\r\n            otpSent: false,\r\n            message: null,\r\n            asyncErrorSubmit: null,\r\n            userExists:\r\n                this.url === \"guest\"\r\n                    ? true\r\n                    : this.url === \"admin\"\r\n                    ? true\r\n                    : this.url === \"vendor\"\r\n                    ? true\r\n                    : false,\r\n            multiUsersExist: false,\r\n            multiUsers: [],\r\n            activeUser: null,\r\n            selectedUser:\r\n                this.url === \"guest\"\r\n                    ? \"guest\"\r\n                    : this.url === \"admin\"\r\n                    ? \"admin\"\r\n                    : this.url === \"vendor\"\r\n                    ? \"vendor\"\r\n                    : null,\r\n            errMsg: null,\r\n        };\r\n    },\r\n    methods: {\r\n        isNumericString(str) {\r\n            return !isNaN(str) && !isNaN(parseFloat(str));\r\n        },\r\n        sendOtp() {\r\n            let _this = this;\r\n            _this.disableLogIn = true;\r\n            let formData = new FormData();\r\n            let link = route(\"sendOtp\");\r\n            formData.append(\"mobile\", _this.email);\r\n            formData.append(\"role\", _this.selectedUser);\r\n\r\n            axios({\r\n                url: link,\r\n                method: \"POST\",\r\n                data: formData,\r\n            })\r\n                .then((response) => {\r\n                    _this.disableLogIn = false;\r\n                    console.log(response.data);\r\n                    if (response.data[\"type\"] == \"success\") {\r\n                        _this.otpSent = true;\r\n                    }\r\n                    _this.message = response.data[\"message\"];\r\n                    setTimeout(function () {\r\n                        _this.message = null;\r\n                    }, 3000);\r\n                })\r\n                .catch((error) => {\r\n                    _this.disableLogIn = false;\r\n                    _this.asyncErrorSubmit = error.response.data.errors;\r\n                    _this.otpSent = false;\r\n                    if (error.response.status == 429) {\r\n                        _this.message =\r\n                            \"Too many attempt! Please try again later\";\r\n                        setTimeout(function () {\r\n                            _this.message = null;\r\n                        }, 3000);\r\n                    }\r\n                });\r\n        },\r\n        retryOtp() {\r\n            let _this = this;\r\n            _this.disableLogIn = true;\r\n            let formData = new FormData();\r\n            let link = route(\"retryOtp\");\r\n            formData.append(\"mobile\", _this.email);\r\n            axios({\r\n                url: link,\r\n                method: \"POST\",\r\n                data: formData,\r\n            })\r\n                .then((response) => {\r\n                    console.log(response.data);\r\n                    _this.disableLogIn = false;\r\n                    if (response.data[\"type\"] == \"success\") {\r\n                        _this.otpSent = true;\r\n                    }\r\n                    if (response.data[\"type\"] == \"error\") {\r\n                        if (response.data[\"message\"] == \"otp_expired\") {\r\n                            _this.otpSent = false;\r\n                            _this.message = \"OTP expired! Please send new OTP!\";\r\n                            return;\r\n                        }\r\n                    }\r\n                    _this.message = response.data[\"message\"];\r\n                    setTimeout(function () {\r\n                        _this.message = null;\r\n                    }, 3000);\r\n                })\r\n                .catch((error) => {\r\n                    _this.disableLogIn = false;\r\n                    _this.asyncErrorSubmit = error.response.data.errors;\r\n                    if (error.response.status == 429) {\r\n                        _this.message = \"Too many attempt!\";\r\n                        setTimeout(function () {\r\n                            _this.message = null;\r\n                        }, 3000);\r\n                    }\r\n                });\r\n        },\r\n        verifyOtp() {\r\n            let _this = this;\r\n            _this.disableLogIn = true;\r\n            let formData = new FormData();\r\n            let link = route(\"verifyOtp\");\r\n            formData.append(\"mobile\", _this.email);\r\n            formData.append(\"role\", _this.selectedUser);\r\n            formData.append(\"otp\", _this.otp);\r\n            axios({\r\n                url: link,\r\n                method: \"POST\",\r\n                data: formData,\r\n            })\r\n                .then((response) => {\r\n                    console.log(response);\r\n                    _this.disableLogIn = false;\r\n                    if (response.data[\"type\"] == \"error\") {\r\n                        if (response.data[\"message\"] == \"OTP expired\") {\r\n                            _this.otpSent = false;\r\n                            _this.message = \"OTP expired! Please send new OTP!\";\r\n                            return;\r\n                        }\r\n                