v-d5dac206]{background:#461952;background-image:url(/assets/login/Group-1821.png);background-position:0 100%;background-repeat:no-repeat;border-radius:16px;padding:55px;width:50%}#login-splide-cont[data-v-d5dac206],.image-view-cont[data-v-d5dac206]{height:100%;width:100%}.image-view-cont[data-v-d5dac206]{-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background:hsla(0,0%,91%,.4);background-position-x:right;background-position-y:bottom;background-repeat:no-repeat;background-size:65%;border:1px solid #fff;border-radius:15px;box-shadow:5px 5px 4px hsla(0,1%,40%,.25);padding:32px 0 0 32px}.image-view-cont>p[data-v-d5dac206]{color:#fff;font-family:Poppins;font-size:30.6966px;font-style:normal;font-weight:500;line-height:46px;width:60%}.forn-grp-dbl input[data-v-d5dac206]{margin-top:0}", "",{"version":3,"sources":["webpack://./resources/js/components/auth/Login.vue"],"names":[],"mappings":"AA+rBA,yBACA,SAAA,CACA,cACA,CACA,kCACA,qBAAA,CAEA,YAAA,CACA,eAAA,CAFA,YAGA,CACA,kCAIA,kBAAA,CAFA,YAAA,CACA,sBAAA,CAFA,SAIA,CACA,kCAEA,YAAA,CACA,qBAAA,CACA,WAAA,CAHA,SAIA,CACA,oCACA,UACA,CACA,qCAMA,aAAA,CALA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAEA,iBACA,CACA,sCAIA,mBAAA,CADA,eAAA,CAEA,gBAAA,CAGA,wBACA,CACA,0EAJA,UAAA,CALA,mBAAA,CACA,iBAAA,CAKA,kBAWA,CARA,oCAIA,mBAAA,CADA,eAAA,CAEA,gBAGA,CACA,mCAMA,aAAA,CACA,YAAA,CACA,qBAAA,CAPA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAIA,kBACA,CACA,yCACA,yBAAA,CACA,wCAAA,CAEA,iCAAA,CADA,iDAAA,CAGA,cAAA,CADA,WAEA,CACA,uCAGA,mBAAA,CADA,WAAA,CAGA,iBAAA,CADA,gBAAA,CAHA,UAKA,CACA,wCAMA,aAAA,CACA,oBAAA,CANA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAGA,gBACA,CACA,+BACA,YAAA,CACA,6BAAA,CACA,aACA,CACA,8BAQA,kBAAA,CAEA,aAAA,CAHA,YAAA,CAEA,gBAAA,CARA,oBAUA,CACA,8HAVA,iBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAqBA,CAfA,gGAaA,UAEA,CACA,sIAPA,kBAAA,CAPA,WAAA,CAQA,iBAAA,CACA,UAAA,CAGA,kBAAA,CAXA,YAAA,CASA,YAkBA,CAdA,sCAaA,cAAA,CAVA,iBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAOA,CACA,6CACA,kBACA,CACA,oCACA,YAAA,CACA,6BAAA,CACA,aACA,CACA,8EAcA,kBAAA,CAVA,eAAA,CACA,8BAAA,CAEA,uBAAA,CADA,uCAAA,CAUA,aAAA,CAIA,cAAA,CAPA,YAAA,CALA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CASA,sBAAA,CAPA,gBAAA,CATA,YAAA,CAYA,gBAAA,CAGA,iBAAA,CAhBA,oBAAA,CAeA,SAIA,CACA,wCACA,UACA,CACA,oFAIA,WAAA,CAFA,gBAAA,CACA,UAEA,CACA,uDACA,iBACA,CACA,kCAOA,aAAA,CANA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CACA,iBAEA,CACA,oCAEA,aAAA,CADA,oBAEA,CACA,oCAEA,uBAAA,CAGA,YAAA,CACA,cAAA,CAHA,eAIA,CACA,sCAGA,eAAA,CAUA,wBAAA,CATA,uBAAA,CAOA,UAAA,CACA,cAAA,CANA,mBAAA,CAGA,kBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAIA,gBAAA,CATA,gBAAA,CAJA,oBAcA,CACA,kCAEA,kBAAA,CAEA,kDAAA,CACA,0BAAA,CACA,2BAAA,CAHA,kBAAA,CAIA,YAAA,CANA,SAOA,CAKA,sEAFA,WAAA,CADA,UAgBA,CAbA,kCAQA,kCAAA,CAAA,0BAAA,CAPA,4BAAA,CAEA,2BAAA,CACA,4BAAA,CAFA,2BAAA,CAGA,mBAAA,CACA,qBAAA,CAGA,kBAAA,CAFA,yCAAA,CAGA,qBAGA,CACA,oCAOA,UAAA,CANA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CACA,SAEA,CACA,qCACA,YACA","sourcesContent":["<template>\r\n    <div class=\"login-main-cont\">\r\n        <div class=\"login-left-cont\">\r\n            <div class=\"login-form-cont\">\r\n                <h1>Welcome to Happywed !</h1>\r\n                <p v-if=\"url === 'host'\">\r\n                    Wedding Organizers! you have access to a range of tools that\r\n                    make managing your wedding events easier. Login now to\r\n                    create and manage your wedding invitations, track RSVPs, and\r\n                    communicate with your guests.\r\n                </p>\r\n                <p v-else-if=\"url === 'vendor'\">\r\n                    Vendors! As a member of our community, you have access to\r\n                    our platform to showcase your professional services to\r\n                    couples seeking your expertise. Simply log in to your\r\n                    account to manage your business and connect with potential\r\n                    clients.\r\n                </p>\r\n                <p v-else-if=\"url === 'guest'\">\r\n                    Guests! you can log in to your account to view your wedding\r\n                    invitation, RSVP, and provide your family member's details,\r\n                    as well as your arrival and departure details. Log in now to\r\n                    ensure your attendance is counted and you don't miss out on\r\n                    any important wedding details.\r\n                </p>\r\n                <p v-else>Welcome! Wedding Community Members</p>\r\n\r\n                <!-- login with password -->\r\n                <form\r\n                    @submit.prevent\r\n                    v-if=\"loginWithOtp == false\"\r\n                    method=\"POST\"\r\n                >\r\n                    <input type=\"hidden\" name=\"_token\" v-bind:value=\"csrf\" />\r\n                    <label for=\"email\" class=\"login-form-group\">\r\n                        E-Mail Address/ Mobile\r\n                        <input\r\n                            type=\"text\"\r\n                            name=\"email\"\r\n                            id=\"email\"\r\n                            v-model=\"email\"\r\n                            @change=\"emptyData()\"\r\n                            @input=\"emptyData()\"\r\n                        />\r\n                        <span\r\n                            v-if=\"errorSubmit && errorSubmit.email\"\r\n                            class=\"errMsg\"\r\n                            >{{ errorSubmit.email[0] }}</span\r\n                        >\r\n                    </label>\r\n\r\n                    <div\r\n                        v-if=\"multiUsersExist == true\"\r\n                        class=\"multiple-users\"\r\n                        style=\"display: flex; justify-content: space-between\"\r\n                    >\r\n                        <span\r\n                            @click=\"\r\n                                selectedUser = item;\r\n                                active = item;\r\n                            \"\r\n                            :class=\"selectedUser == item ? 'active' : ''\"\r\n                            v-for=\"(item, index) in multiUsers\"\r\n                            :key=\"index\"\r\n                        >\r\n                            {{ item }}\r\n                        </span>\r\n                    </div>\r\n                    <div\r\n                        v-if=\"\r\n                            (userExists && multiUsersExist == false) ||\r\n                            selectedUser != null\r\n                        \"\r\n                    >\r\n                        <label for=\"password\" class=\"login-form-group\">\r\n                            Password\r\n                            <input\r\n                                :type=\"showPass\"\r\n                                name=\"password\"\r\n                                id=\"pass\"\r\n                                v-model=\"password\"\r\n                            />\r\n                            <img\r\n                                @click=\"showPass = 'text'\"\r\n                                v-if=\"showPass == 'password'\"\r\n                                src=\"/assets/login/eye.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <img\r\n                                @click=\"showPass = 'password'\"\r\n                                v-if=\"showPass == 'text'\"\r\n                                src=\"/assets/login/eye-off.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <span\r\n                                v-if=\"errorSubmit && errorSubmit.password\"\r\n                                class=\"errMsg\"\r\n                                >{{ errorSubmit.password[0] }}</span\r\n                            >\r\n                        </label>\r\n\r\n                        <div class=\"forn-grp-dbl\">\r\n                            <label\r\n                                for=\"remember_token\"\r\n                                class=\"login-form-group\"\r\n                                style=\"flex-direction: row\"\r\n                            >\r\n                                <input\r\n                                    type=\"checkbox\"\r\n                                    name=\"remember_token\"\r\n                                    id=\"remember_token\"\r\n                                    value=\"true\"\r\n                                    v-model=\"remember_token\"\r\n                                />\r\n                                <span>Keep me logged in</span>\r\n                            </label>\r\n                            <a\r\n                                class=\"forgot-link\"\r\n                                :href=\"route(url + 'password.request')\"\r\n                                >Forgot Password?</a\r\n                            >\r\n                        </div>\r\n                    </div>\r\n                    <span v-if=\"errMsg\" class=\"errMsg\">{{ errMsg }}</span>\r\n                    <button\r\n                        v-if=\"selectedUser == null\"\r\n                        @click=\"checkUserExists()\"\r\n                        type=\"button\"\r\n                        :disabled=\"disableLogIn\"\r\n                    >\r\n                        Continue\r\n                    </button>\r\n                    <button\r\n                        v-if=\"selectedUser != null\"\r\n                        @click=\"loginUser\"\r\n                        type=\"button\"\r\n                        :disabled=\"disableLogIn\"\r\n                    >\r\n                        Log In\r\n                    </button>\r\n                </form>\r\n                <!-- login with otp -->\r\n                <form v-if=\"loginWithOtp == true\" @submit.prevent=\"verifyOtp\">\r\n                    <!-- <input type=\"hidden\" name=\"_token\" v-bind:value=\"csrf\" /> -->\r\n                    <label for=\"email\" class=\"login-form-group\">\r\n                        Mobile\r\n                        <input\r\n                            type=\"text\"\r\n                            name=\"mobile\"\r\n                            id=\"mobile\"\r\n                            v-model=\"email\"\r\n                        />\r\n                        <span\r\n                            v-if=\"asyncErrorSubmit && asyncErrorSubmit.mobile\"\r\n                            class=\"errMsg\"\r\n                            >{{ asyncErrorSubmit.mobile[0] }}</span\r\n                        >\r\n                    </label>\r\n\r\n                    <div\r\n                        v-if=\"multiUsersExist == true\"\r\n                        class=\"multiple-users\"\r\n                        style=\"display: flex; justify-content: space-between\"\r\n                    >\r\n                        <span\r\n                            @click=\"\r\n                                selectedUser = item;\r\n                                active = item;\r\n                            \"\r\n                            v-for=\"(item, index) in multiUsers\"\r\n                            :class=\"selectedUser == item ? 'active' : ''\"\r\n                            :key=\"index\"\r\n                        >\r\n                            {{ item }}\r\n                        </span>\r\n                    </div>\r\n\r\n                    <label\r\n                        v-if=\"otpSent == true\"\r\n                        for=\"otp\"\r\n                        class=\"login-form-group\"\r\n                    >\r\n                        OTP\r\n                        <input type=\"text\" name=\"otp\" id=\"otp\" v-model=\"otp\" />\r\n                        <span\r\n                            v-if=\"errorSubmit && errorSubmit.otp\"\r\n                            class=\"errMsg\"\r\n                            >{{ errorSubmit.otp[0] }}</span\r\n                        >\r\n                    </label>\r\n                    <button\r\n                        @click=\"sendOtp\"\r\n                        v-if=\"otpSent == false\"\r\n                        type=\"button\"\r\n                        :disabled=\"disableLogIn\"\r\n                    >\r\n                        Send Otp\r\n                    </button>\r\n                    <div v-else>\r\n                        <button type=\"submit\" :disabled=\"disableLogIn\">\r\n                            Log In\r\n                        </button>\r\n                        <button\r\n                            :disabled=\"disableLogIn\"\r\n                            @click=\"retryOtp\"\r\n                            type=\"button\"\r\n                        >\r\n                            Resend OTP\r\n                        </button>\r\n                    </div>\r\n                </form>\r\n\r\n                <!-- \"social-login-cont\" was earlier class applied and need to removed  -->\r\n                <div\r\n                    class=\"d-flex justify-content-center\"\r\n                    v-if=\"url == 'host' || url == 'vendor' || url == 'admin'\"\r\n                >\r\n                    <!-- <i class=\"text-small\">login with </i> -->\r\n                    <a class=\"btn btn-sm mx-3\" :href=\"route('googleLogin')\">\r\n                        <img\r\n                            src=\"/assets/login/google.svg\"\r\n                            alt=\"\"\r\n                            style=\"margin-right: 4px\"\r\n                        />\r\n                        <!-- Log in with Google -->\r\n                    </a>\r\n                    <a class=\"btn btn-sm mx-3\" :href=\"route('fblogin')\">\r\n                        <img\r\n                            src=\"/assets/login/logos_facebook.svg\"\r\n                            alt=\"\"\r\n                            style=\"margin-right: 4px\"\r\n                        />\r\n                        <!-- Log in with Facebook-->\r\n                    </a>\r\n                </div>\r\n                <!-- login with OTP button -->\r\n                <div\r\n                    v-if=\"\r\n                        (userExists &&\r\n                            multiUsersExist == false &&\r\n                            loginWithOtp != true &&\r\n                            isNumericString(email)) ||\r\n                        (selectedUser != null &&\r\n                            isNumericString(email) &&\r\n                            loginWithOtp != true)\r\n                    \"\r\n                    class=\"social-login-cont\"\r\n                >\r\n                    <div @click=\"loginWithOtp = true\">\r\n                        <i class=\"fas fa-sms\"></i>\r\n                        Log in with OTP\r\n                    </div>\r\n                </div>\r\n                <!-- login with password button -->\r\n                <div\r\n                    v-if=\"\r\n                        (userExists &&\r\n                            multiUsersExist == false &&\r\n                            loginWithOtp == true) ||\r\n                        (selectedUser != null &&\r\n                            !isNumericString(email) &&\r\n                            loginWithOtp == true) ||\r\n                        loginWithOtp == true\r\n                    \"\r\n                    class=\"social-login-cont\"\r\n                >\r\n                    <div @click=\"loginWithOtp = false\">\r\n                        <i class=\"fas fa-sms\"></i>\r\n                        Log in with Password\r\n                    </div>\r\n                </div>\r\n                <div v-if=\"url == 'host'\" class=\"sinup-link-cont\">\r\n                    Not a member? <a :href=\"route('hostRegister')\">Sign up</a>\r\n                </div>\r\n                <div v-else-if=\"url == 'vendor'\" class=\"sinup-link-cont\">\r\n                    Not a member? <a :href=\"route('vendorRegister')\">Sign up</a>\r\n                </div>\r\n                <div v-else-if=\"url == 'admin'\" class=\"sinup-link-cont\">\r\n                    Not a member? <a :href=\"route('adminRegister')\">Sign up</a>\r\n                </div>\r\n                <div v-else class=\"sinup-link-cont\"></div>\r\n                <!-- <div class=\"login-toggle-cont\">\r\n                    <a :href=\"route('hostLogin')\" v-if=\"active != 'host'\">\r\n                        Host\r\n                    </a>\r\n                    <a :href=\"route('login')\" v-if=\"active != 'guest'\">\r\n                        Guest\r\n                    </a>\r\n                    <a :href=\"route('brideLogin')\" v-if=\"active != 'bride'\">\r\n                        Bride\r\n                    </a>\r\n                    <a :href=\"route('groomLogin')\" v-if=\"active != 'groom'\">\r\n                        Groom\r\n                    </a>\r\n                    <a :href=\"route('vendorLogin')\" v-if=\"active != 'vendor'\">\r\n                        Vendor\r\n                    </a>\r\n                    <a :href=\"route('companyLogin')\" v-if=\"active != 'company'\">\r\n                        Planner\r\n                    </a>\r\n                    <a :href=\"route('memberLogin')\" v-if=\"active != 'member'\">\r\n                        Member\r\n                    </a>\r\n                </div> -->\r\n            </div>\r\n        </div>\r\n        <div class=\"login-pics-cont\">\r\n            <div class=\"splide\" id=\"login-splide-cont\">\r\n                <div class=\"splide__track\">\r\n                    <ul class=\"splide__list\">\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img1.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    The easiest way to get from 'Save the Date'\r\n                                    to 'thanks so much'. Designed and customized\r\n                                    by you.\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img7.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    The online RSVP that's so simple and excited\r\n                                    for you & your guests.\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img6.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    Decide who to celebrate with when and where,\r\n                                    we'll help the rest-from collecting contact\r\n                                    info to managing RSVPs.\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                        <li class=\"splide__slide\">\r\n                            <div\r\n                                class=\"image-view-cont\"\r\n                                style=\"\r\n                                    background-image: url('/assets/login/img5.png');\r\n                                \"\r\n                            >\r\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\r\n                                <p>\r\n                                    Find pre-screened vendors and get\r\n                                    personalised recs. Reach out here and book\r\n                                    when you're ready.\r\n                                </p>\r\n                            </div>\r\n                        </li>\r\n                    </ul>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <FlashMessage :message=\"message\"></FlashMessage>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport FlashMessage from \"@/components/FlashMessage.vue\";\r\n\r\nexport default {\r\n    props: [\"errorSubmit\", \"oldvalues\", \"url\"],\r\n    components: {\r\n        FlashMessage,\r\n    },\r\n    data() {\r\n        return {\r\n            splider: null,\r\n            active: null,\r\n            disableLogIn: false,\r\n            email: this.oldvalues[\"email\"] ?? null,\r\n            mobile: this.oldvalues[\"mobile\"] ?? null,\r\n            password: this.oldvalues[\"password\"] ?? null,\r\n            otp: this.oldvalues[\"otp\"] ?? null,\r\n            remember_token: this.oldvalues[\"remember_token\"] ?? false,\r\n            //formRoute: route(\"hostLogin\"),\r\n            csrf: $('meta[name=\"csrf-token\"]').attr(\"content\"),\r\n            showPass: \"password\",\r\n            loginWithOtp: false,\r\n            otpSent: false,\r\n            message: null,\r\n            asyncErrorSubmit: nul